import React, { Component } from "react";
import { Link } from "react-router-dom";
import { ButtonContainer } from "../Button";

const styles = {
  image: {
    height: "100px"
  }
};

class AddProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "Add Product",
      act: 0,
      index: "",
      datas: []
    };
  }

  componentDidMount() {
    this.refs.title.focus();
  }

  fSubmit = e => {
    e.preventDefault();
    console.log("fsub");

    let datas = this.state.datas;
    let title = this.refs.title.value;
    let img = this.refs.img.value;
    let description = this.refs.description.value;
    let price = this.refs.price.value;
    let quantity = this.refs.quantity.value;

    if (this.state.act === 0) {
      let data = {
        title,
        img,
        description,
        price,
        quantity
      };
      datas.push(data);
    } else {
      let index = this.state.index;
      datas[index].title = title;
      datas[index].img = img;
      datas[index].description = description;
      datas[index].price = price;
      datas[index].quantity = quantity;
    }

    this.setState({
      datas: datas
    });

    this.refs.myForm.reset();
    this.refs.title.focus();
  };

  fRemove = i => {
    let datas = this.state.datas;
    datas.splice(i, 1);
    this.setState({
      datas: datas
    });
    this.refs.myForm.reset();
    this.refs.title.focus();
  };

  fEdit = i => {
    let data = this.state.datas[i];
    this.refs.title.value = data.title;
    this.refs.img.value = data.img;
    this.refs.description.value = data.description;
    this.refs.price.value = data.price;
    this.refs.quantity.value = data.quantity;
    this.setState({
      act: 1,
      index: i
    });
    this.refs.title.focus();
  };

  render() {
    let datas = this.state.datas;
    return (
      <div>
        <h2>{this.state.title}</h2>
        <form ref="myForm" className="myForm">
          <input
            type="text"
            ref="title"
            className="formField"
            placeholder="Title"
          ></input>
          <input
            type="url"
            ref="img"
            className="formField"
            placeholder="Image URL"
          ></input>
          <input
            type="text"
            ref="description"
            className="formField"
            placeholder="Description"
          ></input>
          <input
            type="text"
            ref="price"
            className="formField"
            placeholder="Price"
          ></input>
          <input
            type="text"
            ref="quantity"
            className="formField"
            placeholder="Quantity"
          ></input>
          <ButtonContainer onClick={e => this.fSubmit(e)} className="myButton">
            Save
          </ButtonContainer>
          <Link to="/admin">
            <ButtonContainer>back to Admin</ButtonContainer>
          </Link>
        </form>
        <pre>
          {datas.map((data, i) => (
            <li key={i} className="myList">
              {i + 1} {data.title}
              <img src={data.img} style={styles.image} />
              <button onClick={() => this.fRemove(i)} className="myButtonList">
                remove
              </button>
              <button onClick={() => this.fEdit(i)} className="myButtonList">
                edit
              </button>
            </li>
          ))}
        </pre>
      </div>
    );
  }
}

export default AddProduct;
